.footer {
  background-color: #f1f1f1;
}

.footer-wrapper {
  @media (min-width: 768px) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 51px 0 58px 0;
  }
}

.footer__copyright {
  font-family: "Open Sans", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 19px;
  line-height: 18px;
  color: #000000;
  text-decoration: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 57%;
  padding: 20px 0;
  margin: 0 auto;

  @media (min-width: 768px) {
    width: 27%;
    margin: 0;
    padding: 0;
  }

  @media (min-width: 1150px) {
    width: 18%;
  }

  .footer__copyright-info {
    @media (min-width: 768px) {
      padding: 0 5px 5px 0;
    }

    @media (min-width: 1150px) {
      padding-left: 5px;
      padding-bottom: 5px;
    }
  }

  &:hover svg {
    fill: #46c1ae;
  }

  &:active svg {
    fill: #46c1ae;
    opacity: 0.3;
  }
}

.footer__copyright svg {
  fill: #63d1bb;
}

.footer-logo svg {
  &:hover {
    fill: #46c1ae;
  }

  &:active {
    fill: #46c1ae;
    opacity: 0.3;
  }
}
